.header-wrapper {
    display: flex;
    align-items: center;
    padding: 8px;
    text-align: center;
    justify-content: center;
    position: sticky;
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 0  0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(2px);
    -webkit-backdrop-filter: blur(2px);
    top: 0;
  }
  
  .header-wrapper li {
    list-style: none;
  }
  .header-wrapper nav ul {
    display: flex;
    gap: 10px;
  }
  .header-wrapper nav li a {
    text-decoration: none;
    font-weight: 400;
    letter-spacing: 0.5px;
    padding: 6px 10px;
    border-radius: 6px;
    color: #000;
    font-size: 14px;
  }
  .header-wrapper img{
   border-radius: 50%;
  }